import Vue from 'vue'
// 导入路由模块
import Router from 'vue-router'

// 导入components , 路由懒加载
// import Login from './components/Login'
const Login = () => import(/* webpackChunkName:"login_home_welcome" */ './components/Login.vue')
// import Home from './components/Home'
const Home = () => import(/* webpackChunkName:"login_home_welcome" */ './components/Home')
// import Welcome from './components/Welcome'
const Welcome = () => import(/* webpackChunkName:"login_home_welcome" */ './components/Welcome')

// import Users from './components/user/Users'
const Users = () => import(/* webpackChunkName:"users" */ './components/user/Users')

// import Roles from './components/power/Roles'
const Roles = () => import(/* webpackChunkName:"power" */ './components/power/Roles')
// import Rights from './components/power/Rights'
const Rights = () => import(/* webpackChunkName:"power" */ './components/power/Rights')

// import Categories from './components/goods/Categories.vue'
const Categories = () => import(/* webpackChunkName:"goods" */ './components/goods/Categories.vue')
// import Params from './components/goods/Params.vue'
const Params = () => import(/* webpackChunkName:"goods" */ './components/goods/Params.vue')
// import List from './components/goods/List.vue'
const List = () => import(/* webpackChunkName:"goods" */ './components/goods/List.vue')
// import Add from './components/goods/Add.vue'
const Add = () => import(/* webpackChunkName:"goods" */ './components/goods/Add.vue')

// import Orders from './components/order/Orders'
const Orders = () => import(/* webpackChunkName:"orders" */ './components/order/Orders')

// import Reports from './components/report/Reports'
const Reports = () => import(/* webpackChunkName:"reports" */ './components/report/Reports')

// 全局注册路由模块
Vue.use(Router)

// 定义路由映射
const routes = [
	{ path: '/', redirect: '/login' },
	{ path: '/login', component: Login },
	{
		path: '/home',
		component: Home,
		redirect: '/welcome',
		children: [
			{ path: '/welcome', component: Welcome },
			{ path: '/users', component: Users },
			{ path: '/roles', component: Roles },
			{ path: '/rights', component: Rights },
			{ path: '/categories', component: Categories },
			{ path: '/params', component: Params },
			{ path: '/goods', component: List },
			{ path: '/goods/add', component: Add },
			{ path: '/orders', component: Orders },
			{ path: '/reports', component: Reports }
		]
	}
]

// 创建router实例
const router = new Router({
	routes,
	mode: 'history'
})

// 设置路由导航守卫
router.beforeEach((to, from, next) => {
	// 登录界面，继续
	if (to.path === '/login') return next()
	// 非登录界面，验证token
  const tokenStr = window.sessionStorage.getItem('token')
	// 不存在则跳转登录界面
  if (!tokenStr) return next('/login')
  next()
})

export default router